<template>
  <el-form ref="form" :model="form" :rules="rules" class="_creditcard __form-sm">
    <el-form-item
      :label="$t('card.number')"
      prop="bin"
      class="_creditcard-number"
      :class="{ '_creditcard-valid': validatedNumber }">
      <el-input
        type="tel"
        v-model="form.bin"
        v-mask="cardMask"
        v-on-keyup="getBrand"></el-input>
    </el-form-item>
    <div class="_creditcard-icons">
      <i v-for="brand in brands" :class="[ brand, { active: activeBrand === brand }]"></i>
    </div>

    <el-form-item :label="$t('card.name')" prop="name">
      <el-input v-model="form.name" ref="name"></el-input>
    </el-form-item>
    <el-collapse-transition>
      <div key="holder-info" v-if="!hideHolderFields">
        <el-form-item :label="$t('account.birth')" prop="birth">
          <el-input
            v-model="form.birth"
            v-mask="$country !== 'br' ? '9999-99-99' : '99/99/9{2,4}'"
            type="tel"
            class="__input-sm">
          </el-input>
        </el-form-item>
        <el-form-item :label="$t('card.holderDoc')" prop="doc">
          <el-input
            v-model="form.doc"
            v-mask="$country !== 'br' ? '9{5,19}' : brDocMask"
            type="tel"
            class="__input-sm">
          </el-input>
        </el-form-item>
      </div>
    </el-collapse-transition>

    <el-form-item :label="$t('card.validate')" prop="validate">
      <el-input
        type="tel"
        v-model="form.validate"
        v-mask="'99 / 99'"
        placeholder="02 / 22"
        class="__input-sm"></el-input>
    </el-form-item>
    <el-form-item :label="$t('card.securityCode')" prop="cvv">
      <el-input
        type="tel"
        v-model="form.cvv"
        v-mask="'9{3,4}'"
        placeholder="123"
        class="__input-sm">
        <template slot="append">
          <el-popover
            placement="top"
            width="180"
            trigger="click">
            <el-button slot="reference">
              <a-icon icon="question-circle"></a-icon>
            </el-button>
            <div class="_creditcard-cvv-help" v-if="activeBrand === ''">
              {{ $t('card.cvvHelp') }}
            </div>
            <div class="_creditcard-cvv-help" v-else-if="activeBrand === 'american-express'">
              <p>{{ $t('card.cvvHelpAmex') }}</p>
              <div class="_creditcard-cvv-imgs"><div class="amex"></div></div>
            </div>
            <div class="_creditcard-cvv-help" v-else>
              <p>{{ $t('card.cvvHelpNonAmex') }}</p>
              <div class="_creditcard-cvv-imgs"><div class="non-amex"></div></div>
            </div>
          </el-popover>
        </template>
      </el-input>
    </el-form-item>

    <el-form-item
      v-if="handleInstallments"
      :label="$t('card.installment')"
      prop="installment">
      <el-select
        v-model="form.installment"
        class="__input-sm"
        :placeholder="$t('card.installmentNumber')">
        <el-option
          :label="formatMoney(checkout.amount.total) + ' ' + $t('card.atSight')"
          :value="1">
        </el-option>
        <el-option
          v-for="option in handleInstallments"
          :key="option.number"
          :label="option.number + 'x' +
            $t('general.of') + formatMoney(option.value) +
            (option.tax ? '' : ' ' + $t('card.interestFree'))"
          :value="option.number">
        </el-option>
      </el-select>
    </el-form-item>

    <el-form-item size="mini">
      <el-checkbox v-model="sameAddress" class="_creditcard-same-address">
        {{ $t('card.sameAddress') }}
      </el-checkbox>
    </el-form-item>
    <el-collapse-transition>
      <address-form
        key="address-form"
        ref="address"
        v-if="!sameAddress"
        @submit-form="(data) => { submitForm(data) }"
        :noButton="true"
        :noRecipient="true"/>
    </el-collapse-transition>

    <el-form-item size="large">
      <el-button
        type="success"
        @click="() => { submitForm() }"
        class="_creditcard-pay __btn-block">
        <a-icon icon="lock" class="__icon-mr"></a-icon>
        {{ $t('card.checkout') }}
      </el-button>
      <img v-if="stampImg" :src="stampImg" class="_creditcard-stamp"/>
    </el-form-item>
  </el-form>
</template>

<script>
import AddressForm from '@/components/routes/account/AddressForm'
import cardValidator from 'card-validator'
import isValidCpf from '@brazilian-utils/is-valid-cpf'
import isValidCnpj from '@brazilian-utils/is-valid-cnpj'
import { mapGetters } from 'vuex'
import { addRule, checkMask, checkDate, isoDate, formatMoney, compareStrings } from '@/lib/utils'

export default {
  name: 'CreditCard',

  components: {
    AddressForm
  },

  props: [
    'checkHolder',
    'skipHolderInfo',
    'installmentOptions',
    'jsClient',
    'stampImg'
  ],

  computed: mapGetters([
    'checkout'
  ]),

  data () {
    // require instalment number if options are defined
    let handleInstallments
    if (this.installmentOptions && this.installmentOptions.length) {
      handleInstallments = this.installmentOptions.concat().sort((a, b) => {
        // sort installment options by number
        return a.number - b.number
      })
    }

    // setup form validation rules
    let rules = {}
    // handle required form fields
    let required = [ 'bin', 'name', 'validate', 'cvv' ]
    if (handleInstallments) {
      required.push('installment')
    }
    required.forEach((label) => {
      addRule(label, { required: true, message: this.$t('validate.required') }, rules)
    })
    // min field length for holder name
    addRule('name', { min: 3, message: this.$t('card.fullNameAlert'), trigger: 'blur' }, rules)
    if (!this.skipHolderInfo) {
      // handle masked inputs validation
      ;[ 'doc', 'birth'  ].forEach((label) => {
        addRule(label, { validator: checkMask(this.$t('validate.mask')), trigger: 'blur' }, rules)
      })
      // handle date validation
      addRule('birth', { validator: checkDate(this.$t('validate.date')), trigger: 'blur' }, rules)
    }

    // check if card number is at least potentially valid
    addRule('bin', {
      validator: (rule, value, cb) => {
        if (cardValidator.number(value).isPotentiallyValid) {
          // can be valid
          cb()
        } else {
          // number not valid at all
          cb(new Error(this.$t('card.number') + ' ' + this.$t('validate.isInvalid')))
        }
      },
      trigger: 'blur'
    }, rules)

    // check date format and expiration
    addRule('validate', {
      validator: (rule, value, cb) => {
        if (cardValidator.expirationDate(value).isValid) {
          // date valid and not expired
          cb()
        } else {
          let [ month, year ] = value.split(' / ')
          if (!cardValidator.expirationMonth(month).isValid) {
            cb(new Error(this.$t('card.invalidMonth')))
          } else if (!cardValidator.expirationYear(year).isValid) {
            cb(new Error(this.$t('card.invalidYear')))
          } else {
            // month past on current year
            cb(new Error(this.$t('card.monthPast')))
          }
        }
      },
      trigger: 'blur'
    }, rules)

    // validate CVV or CID code
    addRule('cvv', {
      validator: (rule, value, cb) => {
        // need card brand to validate CVV
        if (this.activeBrand !== '') {
          if (this.activeBrand !== 'american-express') {
            if (cardValidator.cvv(value).isValid) {
              // valid 3 digits CVV
              cb()
            } else {
              cb(new Error(this.$t('card.cvvInvalidNonAmex')))
            }
          } else if (cardValidator.cvv(value, 4).isValid) {
            // valid Amex CID with 4 digits
            cb()
          } else {
            cb(new Error(this.$t('card.cvvInvalidAmex')))
          }
        } else if (value.replace(/\D/g, '').length < 3) {
          // code always invalid for any card brand
          cb(new Error(this.$t('card.cvvInvalid')))
        }
      },
      trigger: 'blur'
    }, rules)

    return {
      brands: [
        'visa',
        'mastercard',
        'american-express',
        'elo',
        'diners-club',
        'hiper',
        'hipercard'
      ],
      activeBrand: '',
      validatedNumber: false,
      form: {
        bin: '',
        name: '',
        validate: '',
        cvv: '',
        birth: '',
        doc: '',
        installment: ''
      },
      rules,
      sameAddress: true,
      sameHolder: false,
      hideHolderFields: !!(this.skipHolderInfo || this.checkHolder),
      handleInstallments,

      cardMask: {
        mask: [
          // array of cc number formats
          // https://baymard.com/checkout-usability/credit-card-patterns
          // Most common format and non Amex
          'G9{3} 9{4} 9{4} 9{4}',
          // American Express
          '3A9{2} 9{6} 9{5}',
          // Dinners
          '3D9{2} 9{6} 9{4}',
          // general matching from 12 to 19 digits
          '9{4} 9{4} 9{4}[ 9{1,4}][ 9{1,3}]'
        ],
        definitions: {
          'G': { validator: '[124-9]' },
          '3': { validator: '[3]' },
          'A': { validator: '[47]' },
          'D': { validator: '[0689]' }
        }
      },

      brDocMask: [
        // personal doc
        '999.999.999-99',
        // business doc
        '99.999.999/9999-99'
      ]
    }
  },

  methods: {
    formatMoney (value) {
      return formatMoney(value, this.$currency, this.$lang)
    },

    getBrand () {
      // preset invalid
      this.validatedNumber = false
      // get card brand from number
      let valid = cardValidator.number(this.form.bin.replace(/\D/g, ''))
      if (valid.isPotentiallyValid && valid.card) {
        let brand = valid.card.type
        if (this.activeBrand !== brand) {
          // brand changed
          this.activeBrand = brand
          if (this.form.cvv !== '') {
            // must revalidate CVV
            this.$refs.form.validateField('cvv')
          }
        }
        if (valid.isValid) {
          this.validatedNumber = true
        }
      } else {
        // unset last active brand
        this.activeBrand = ''
      }
    },

    submitForm (address, confirmed) {
      if (!this.sameAddress && (!address || !address.zip)) {
        // submit address form first and wait for address data
        this.$refs.address.submitForm()
      } else {
        this.$refs.form.validate((valid) => {
          let notify
          if (valid) {
            // validated from form rules
            let data = this.form

            let { birth, doc } = data
            if (doc !== '') {
              // check document number
              if (!isValidCnpj(doc) && !isValidCpf(doc)) {
                // invalid personal document number
                notify = this.$t('account.personalDoc')
              }
            }
            if (birth !== '') {
              // parse birth date string to ISO format
              data.birth = isoDate(birth)
            }

            if (!notify) {
              // check credit card number
              let valid = cardValidator.number(data.bin)
              if (valid.isValid || confirmed === true) {
                let hash
                new Promise(resolve => {
                  if (this.jsClient) {
                    this.jsClient.loaded.then(() => {
                      let cardHash = this.jsClient.cc_hash
                      if (cardHash) {
                        let fn = window[cardHash.function]
                        if (typeof fn === 'function') {
                          let [ month, year ] = data.validate.split(' / ')
                          // generate hash with card data
                          let cardData = {
                            name: data.name,
                            doc: data.doc,
                            number: data.bin,
                            cvc: data.cvv,
                            month,
                            year
                          }

                          if (!cardHash.is_promise) {
                            hash = fn(cardData)
                          } else {
                            fn(cardData).then(token => {
                              hash = token
                              resolve()
                            })
                            return
                          }
                        }
                      }
                      resolve()
                    })
                  } else {
                    resolve()
                  }
                }).then(() => {
                  // handle submit
                  this.$emit('submit-form', Object.assign(data, { address, hash }))
                })
                return
              } else if (valid.isPotentiallyValid) {
                // not sure
                // must confirm card number
                this.$confirm(this.$t('card.potentiallyValid'), data.bin, {
                  cancelButtonText: this.$t('card.confirmNumber'),
                  confirmButtonText: this.$t('card.editNumber'),
                  type: 'info'
                }).catch(() => {
                  // ignore and submit again
                  this.submitForm(address, true)
                })
                return
              } else {
                // invalid card number
                notify = this.$t('card.number')
              }
            }
          }

          if (notify) {
            // complete notification message
            notify += this.$t('validate.isInvalid')
          }
          // show notification
          this.$message({
            showClose: true,
            message: notify || this.$t('card.invalidForm'),
            type: 'warning'
          })
        })
      }
    }
  },

  mounted () {
    if (!this.skipHolderInfo && this.checkHolder) {
      // compare holder name with customer name
      this.$refs.name.handleChange = () => {
        let data = this.form
        // compare first name
        let name = data.name.trim()
        if (name === '') {
          // ampty holder name
          this.hideHolderFields = true
        } else if (compareStrings(this.checkHolder, name.replace(/(\s.*)/, ''))) {
          // holder name matched
          this.hideHolderFields = true
          // reset doc and birth fields
          data.doc = data.birth = ''
        } else {
          this.hideHolderFields = false
        }
      }
    }

    if (this.jsClient) {
      // load gateway JS
      let script = document.createElement('script')
      script.async = true
      script.defer = true
      this.jsClient.loaded = new Promise(resolve => {
        script.onload = () => {
          resolve()
          // run js client onload expression
          let expression = this.jsClient.onload_expression
          if (expression) {
            try {
              eval(expression)
            } catch (err) {
              console.error(err, expression)
            }
          }
        }
      })
      script.setAttribute('src', this.jsClient.script_uri)
      document.head.appendChild(script)
    }
  }
}
</script>

<style lang="scss">
// Element UI theme variables
@import '../../../../node_modules/element-theme-chalk/src/common/var.scss';

._creditcard {
  max-width: 550px;
}
._creditcard-number.is-success input {
  border-color: $--border-color-hover !important;
}
._creditcard-number:not(.is-error) input:focus {
  border-color: $--color-primary !important;
}
._creditcard-number._creditcard-valid input {
  border-color: $--color-success !important;
}
._creditcard-icons {
  margin: -$--card-padding * .25 auto $--card-padding auto;
  text-align: center;
}
._creditcard-icons i {
  width: 40px;
  height: 25px;
  background-image: url('../../../../static/payments.png');
  background-repeat: no-repeat;
  display: inline-block;
  margin: 3px 3px 0 0;
  transition: $--fade-linear-transition;
  opacity: .4;
}
._creditcard-icons i.active {
  opacity: 1;
}
._creditcard-icons .visa {
  background-position: 0 0;
}
._creditcard-icons .mastercard {
  background-position: 0 -50px;
}
._creditcard-icons .hipercard {
  background-position: 0 -125px;
}
._creditcard-icons .hiper {
  background-position: 0 -150px;
}
._creditcard-icons .elo {
  background-position: 0 -175px;
}
._creditcard-icons .diners-club {
  background-position: 0 -200px;
}
._creditcard-icons .american-express {
  background-position: 0 -375px;
}
._creditcard-cvv-help {
  text-align: left;
}
._creditcard-cvv-imgs div {
  width: 150px;
  height: 93px;
  background-image: url('../../../../static/cvv.png');
  background-repeat: no-repeat;
  display: block;
  margin: 0 auto;
}
._creditcard-cvv-imgs .non-amex {
  background-position: 0 0;
}
._creditcard-cvv-imgs .amex {
  background-position: 0 -93px;
}
._creditcard-same-address {
  white-space: normal;
}
._creditcard-stamp {
  margin-top: $--card-padding * .75;
  display: block;
}
</style>



