// Font Awesome Icons
// https://fontawesome.com/how-to-use/use-with-node-js
import fontawesome from '@fortawesome/fontawesome'

// choose icons
import faAngleLeft from '@fortawesome/fontawesome-free-solid/faAngleLeft'
import faAngleRight from '@fortawesome/fontawesome-free-solid/faAngleRight'
import faAngleDown from '@fortawesome/fontawesome-free-solid/faAngleDown'
import faAngleUp from '@fortawesome/fontawesome-free-solid/faAngleUp'
import faCheck from '@fortawesome/fontawesome-free-solid/faCheck'
import faPlus from '@fortawesome/fontawesome-free-solid/faPlus'
import faTimes from '@fortawesome/fontawesome-free-solid/faTimes'
import faListUl from '@fortawesome/fontawesome-free-solid/faListUl'
import faUserCircle from '@fortawesome/fontawesome-free-solid/faUserCircle'
import faLock from '@fortawesome/fontawesome-free-solid/faLock'
import faCommentAlt from '@fortawesome/fontawesome-free-solid/faCommentAlt'
import faShoppingBag from '@fortawesome/fontawesome-free-solid/faShoppingBag'
import faTruck from '@fortawesome/fontawesome-free-solid/faTruck'
import faShippingFast from '@fortawesome/fontawesome-free-solid/faShippingFast'
import faCreditCard from '@fortawesome/fontawesome-free-solid/faCreditCard'
import faBarcode from '@fortawesome/fontawesome-free-solid/faBarcode'
import faHome from '@fortawesome/fontawesome-free-solid/faHome'
import faGift from '@fortawesome/fontawesome-free-solid/faGift'
import faEdit from '@fortawesome/fontawesome-free-solid/faEdit'
import faTrash from '@fortawesome/fontawesome-free-solid/faTrash'
import faCheckCircle from '@fortawesome/fontawesome-free-solid/faCheckCircle'
import faCircleNotch from '@fortawesome/fontawesome-free-solid/faCircleNotch'
import faTicketAlt from '@fortawesome/fontawesome-free-solid/faTicketAlt'
import faQuestionCircle from '@fortawesome/fontawesome-free-solid/faQuestionCircle'
import faPrint from '@fortawesome/fontawesome-free-solid/faPrint'
import faCopy from '@fortawesome/fontawesome-free-solid/faCopy'
import faMapMarkerAlt from '@fortawesome/fontawesome-free-solid/faMapMarkerAlt'

// setup icons to library
fontawesome.library.add(
  faAngleLeft,
  faAngleRight,
  faAngleDown,
  faAngleUp,
  faCheck,
  faPlus,
  faTimes,
  faListUl,
  faUserCircle,
  faLock,
  faCommentAlt,
  faShoppingBag,
  faTruck,
  faShippingFast,
  faCreditCard,
  faBarcode,
  faHome,
  faGift,
  faEdit,
  faTrash,
  faCheckCircle,
  faCircleNotch,
  faTicketAlt,
  faQuestionCircle,
  faPrint,
  faCopy,
  faMapMarkerAlt
)



