<template>
  <div class="_account">
    <transition name="fade">
      <div key="user-logged" v-if="isCustomerLogged" class="_account-logged">
        <h1 class="_account-title">
          {{ $t('session.hi') + ' ' + customer.display_name }}
          <small>
            {{ $t('session.isNotYou') + '?' }}
            <a href="javascript:;" @click="logout" class="_account-logout">
              {{ $t('session.logout') }}
            </a>
          </small>
        </h1>

        <el-tabs :value="$route.name" class="_account-tabs" @tab-click="changeTab">
          <el-tab-pane :label="$t('account.registration')" name="account"></el-tab-pane>
          <el-tab-pane :label="$t('account.addresses')" name="addresses"></el-tab-pane>
          <el-tab-pane :label="$t('account.orders')" name="orders"></el-tab-pane>
        </el-tabs>

        <transition name="fade">
          <router-view/>
        </transition>
      </div>

      <div key="user-unlogged" v-else class="_account-unlogged">
        <h1 class="_account-title">
          {{ $t('session.account') }}
          <small>{{ $t('account.unloggedMsg') }}</small>
        </h1>
        <el-button type="primary" @click="login" class="_account-login">
          {{ $t('session.identify') }}
        </el-button>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import { updateTitle } from '@/lib/utils'

export default {
  name: 'MyAccount',

  computed: mapGetters([
    'customer',
    'isCustomerLogged',
    'shopName'
  ]),

  methods: {
    ...mapActions([
      'login'
    ]),
    ...mapMutations([
      'logout'
    ]),

    changeTab (tab) {
      // redirect to current tab
      this.$router.push({ name: tab.name })
    }
  },

  created () {
    // update header title
    updateTitle(this.$t('session.account'), this.shopName)
  }
}
</script>

<style lang="scss">
// Element UI theme variables
@import '../../../node_modules/element-theme-chalk/src/common/var.scss';

._account-logout {
  color: $--color-danger;
}
._account-tabs {
  margin-bottom: 30px;
}
</style>



